@use 'base/mixins' as m;
@use 'base/variables' as v;

.alertas{
    padding: 1rem 1rem 1rem 2rem;
    font-weight: 900;
  
    margin: 1rem 0;
    max-width: 80rem;

    &.error{
        background-color: lighten($color: v.$rojo, $amount: 60%);
        border-left: .5rem solid v.$rojo;
        color: v.$rojo;
    }

    &.exito{
        background-color: lighten($color: v.$verde, $amount: 60%);
        border-left: .5rem solid v.$verde;
        color: v.$verde;
    }
}